const initState = {
    //
    audioInfo:{
        audioFlag:false,//
        audioPlayBtn:false,//
        SongInfo:{//
            SongId:'',
            SongName:'',
            SongPic:'',
            SongArtists:''
        },
        SongListFlag:false,
        SongList:[],//
    },
    //end
}
export const reducer = (state = initState,action) => {
    // console.log("reducer",state,action)
    switch (action.type) {
        case 'setAudioFlag':
            state.audioInfo.audioFlag = action.value
            return state
        case 'setAudioPlayBtn':
            return {
                audioInfo:{
                    ...state.audioInfo,
                    audioPlayBtn:action.value
                }
            }
        case 'setAudioInfo':
            if(state.audioInfo.SongInfo.SongId === action.SongInfo.SongId){
                return state
            }
            state.audioInfo.SongInfo = action.SongInfo
            state.audioInfo.SongList = action.SongList
            return state
        case 'setSongInfo':
            if(state.audioInfo.SongInfo.SongId === action.value.SongId){
                return state
            }
            return {
                audioInfo:{
                    ...state.audioInfo,
                    SongInfo:{...action.value}
                }
            }
        case 'setSongList':
            state.audioInfo.SongList = action.value
            return state
        case 'setSongInfoInit':
            state.audioInfo.SongInfo = action.value
            return state
        default:
            return state
    }
}
